<script setup>
import { ref } from 'vue'

const props = defineProps({
	fontSize: {
		type: String,
		default: '1em',
	},
	padding: {
		type: String,
		default: '1vw 2vw',
	},
	color: {
		type: String,
		default: 'black',
	},
	backgroundColor: {
		type: String,
		default: '#005cfe',
	},
	hoverBackgroundColor: {
		type: String,
		default: '#1a6cff', // Цвет при наведении
	},
	fontWeight: {
		type: String,
		default: '500',
	},
})

const isHovered = ref(false)
</script>

<template>
	<button
		:style="{
			fontSize: fontSize,
			padding: padding,
			color: color,
			fontWeight: fontWeight,
			backgroundColor: isHovered ? hoverBackgroundColor : backgroundColor,
		}"
		@mouseenter="isHovered = true"
		@mouseleave="isHovered = false"
	>
		<slot></slot>
	</button>
</template>

<style scoped>
button {
	padding: 1vw 2vw;
	border-radius: 1vw;
	cursor: pointer;
	transition: background-color 0.3s;
}
</style>
