<script setup>
import balaclava from '@/assets/block_1/balaclava.jpeg'
import boots from '@/assets/block_1/boots.jpeg'
import cream from '@/assets/block_1/cream.jpeg'
import glasses from '@/assets/block_1/glasses.jpeg'
import gloves from '@/assets/block_1/gloves.jpeg'
import hemlet from '@/assets/block_1/hemlet.jpeg'
import protective_mask from '@/assets/block_1/protective_mask.jpeg'
import protective_suit from '@/assets/block_1/protective_suit.jpeg'
import work_clothes from '@/assets/block_1/work_clothes.jpeg'

import Button from '../UI/Button.vue'

import { ref, computed, onMounted } from 'vue'

const emit = defineEmits(['end'])

const taskArr = ref([
	{
		imgArr: [
			{
				img: hemlet,
				description: 'Каска защитная',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: work_clothes,
				description: 'Защитный костюм от общих производственных загрязнений',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: cream,
				description: 'Защитный крем',
				isCorrect: false,
				isActive: false,
				isFail: null,
			},
			{
				img: glasses,
				description: 'Очки защитные',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
		],
		isFail: null,
		badMsg:
			'Неверно :( От данной опасности защитят каска, защитный костюм от общих производственных загрязнений и защитные очки',
		goodMsg:
			'Верно! От данной опасности защитят каска, защитный костюм от общих производственных загрязнений и защитные очки',
		task: 'Выберите СИЗы, которые защитят от механических повреждений',
	},
	{
		imgArr: [
			{
				img: hemlet,
				description: 'Каска защитная',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: glasses,
				description: 'Очки защитные',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: boots,
				description: 'Ботинки кожаные с защитным подноском',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: balaclava,
				description: 'Подшлемник термостойкий',
				isCorrect: false,
				isActive: false,
				isFail: null,
			},
		],
		isFail: null,
		badMsg:
			'Неверно :( ОТ данной опасности защитят защитные очки, ботинки кожаные с защитным подноском и защитная каска. Это прописано в п 4.2 Приложения № 2 к ЕТН',
		goodMsg:
			'Верно! ОТ данной опасности защитят защитные очки, ботинки кожаные с защитным подноском и защитная каска. Это прописано в п 1.8 Приложения № 2 к ЕТН',
		task: 'Выберите СИЗы, которые защитят от столкновения работника с неподвижным предметом',
	},
	{
		imgArr: [
			{
				img: protective_mask,
				description: 'Щиток защитный',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: gloves,
				description: 'Защитные перчатки',
				isCorrect: false,
				isActive: false,
				isFail: null,
			},
			{
				img: glasses,
				description: 'Очки защитные',
				isCorrect: true,
				isActive: false,
				isFail: null,
			},
			{
				img: protective_suit,
				description: 'Костюм изолирующий',
				isCorrect: false,
				isActive: false,
				isFail: null,
			},
		],
		isFail: null,
		badMsg:
			'Неверно :( От этой опасности защитят защитные очки и защитный щиток. Это прописано в п 1.24 Приложения № 2 к ЕТН',
		goodMsg:
			'Верно! От этой опасности защитят защитные очки и защитный щиток. Это прописано в п 1.24 Приложения № 2 к ЕТН',
		task: 'Выберите СИЗы, которые защитят от мелкой стружки, осколков, крупнодисперсной пыли',
	},
])

const isNextButtonDisabled = ref(false)
const index = ref(0)
const countUserSelect = computed(() => {
	return taskArr.value[index.value].imgArr.filter(
		item => item.isActive === true
	).length
})

const userChoose = e => {
	if (taskArr.value[index.value].isFail !== null) return
	if (e.isActive) e.isActive = false
	else e.isActive = true
}

const checkUserAnswer = () => {
	const currentTask = taskArr.value[index.value]

	// Проверяем, есть ли неправильно выбранные элементы или пропущены правильные
	const hasIncorrectSelection = currentTask.imgArr.some(
		item => item.isActive && !item.isCorrect
	)
	const hasMissingCorrect = currentTask.imgArr.some(
		item => !item.isActive && item.isCorrect
	)

	// Устанавливаем isFail в зависимости от результатов проверки
	currentTask.isFail = hasIncorrectSelection || hasMissingCorrect
}

const next = () => {
	if (isNextButtonDisabled.value) return // Если кнопка заблокирована, выходим
	isNextButtonDisabled.value = true // Блокируем кнопку

	if (index.value < taskArr.value.length - 1) {
		index.value += 1
	}

	// Разблокируем кнопку через 500 мс
	setTimeout(() => {
		isNextButtonDisabled.value = false
	}, 500)
}
const preloadImages = () => {
	const imageUrls = [
		balaclava,
		boots,
		cream,
		glasses,
		gloves,
		hemlet,
		protective_mask,
		protective_suit,
		work_clothes,
	]
	imageUrls.forEach(url => {
		const img = new Image()
		img.src = url
	})
}

const nextBlock = () => {
	emit(
		'end',
		taskArr.value.some(item => item.isFail === true)
	)
}

onMounted(() => {
	preloadImages()
})
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок 1: СИЗ</h2>
			</div>
			<div class="header_task">
				<h3>
					В этом разделе вас ждет три ситуационных задач по СИЗ. Выбирайте,
					какие СИЗ подойдут для защиты от разных опасностей согласно Единым
					типовым нормам. Вперед!
				</h3>
			</div>
			<div class="header__task">
				<Transition mode="out-in">
					<h4 :key="index">
						{{ taskArr[index].task }} <br />
						Выберите несколько верных ответов
					</h4>
				</Transition>
			</div>
		</div>

		<Transition mode="out-in">
			<div :key="index" class="container__img">
				<div
					v-for="(item, index) in taskArr[index].imgArr"
					class="container__img_item"
					:id="index"
					:class="{ active: item.isActive }"
					@click="userChoose(item)"
				>
					<img :src="item.img" alt="" />
					<div class="container__img-description">{{ item.description }}</div>
				</div>
			</div>
		</Transition>

		<Transition mode="out-in">
			<div
				v-if="countUserSelect > 0 && taskArr[index].isFail === null"
				class="container__bottom"
			>
				<div>
					<Button @click="checkUserAnswer" color="#fafafa">Проверить</Button>
				</div>
			</div>
			<div v-else-if="taskArr[index].isFail !== null">
				<div class="hit">
					<div class="bad" v-if="taskArr[index].isFail">
						{{ taskArr[index].badMsg }}
					</div>
					<div class="good" v-else>
						{{ taskArr[index].goodMsg }}
					</div>
					<div class="btn">
						<Button
							@click="next"
							v-if="taskArr.length > index + 1"
							color="#fafafa"
							:disabled="isNextButtonDisabled"
							>Далее</Button
						>
						<Button color="#fafafa" font-weight="900" v-else @click="nextBlock"
							>Перейти к блоку Несчастные случаи</Button
						>
					</div>
				</div>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__img {
	margin: 2vw;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 2vw;
	text-align: center;
}

.container__img img {
	max-width: 20vw;
	max-height: 14vw;
	margin: 0 auto;
}

.container__img_item {
	display: grid;
	grid-template-rows: 1fr 1fr;
	justify-content: center;
	border: 0.25vw solid black;
	border-radius: 1vw;
	cursor: pointer;
	transition: border 0.1s;
	transition: scale 0.5s;
}

.container__img_item:hover {
	scale: 0.95;
}

.active {
	border: 0.25vw solid #3b8eed;
}

.container__bottom {
	display: flex;
	flex-flow: column;
	align-items: center;
}

.btn {
	margin-top: 3vw;
	display: flex;
	justify-content: center;
}

.good {
	margin-left: 2vw;
	color: #42d072;
}

.bad {
	margin-left: 2vw;
	color: red;
}

.container__img-description {
	padding: 0 1vw;
}

.header__task h4 {
	text-align: center;
	font-size: 1.1em;
}
</style>
