<script setup>
import Button from '../UI/Button.vue'
import { ref, onMounted } from 'vue'
import draggable from 'vuedraggable'

const emit = defineEmits(['end'])

const taskArr = ref([
	{
		task: 'Выберите аккредитованную организацию и заключите договор',
		position: 1,
	},
	{
		task: 'Утвердите состав комиссии по проведению СОУТ и график проведения',
		position: 2,
	},
	{ task: 'Составьте список рабочих мест для оценки', position: 3 },
	{ task: 'Подпишите отчет по результатам СОУТ', position: 4 },
	{ task: 'Разместите результаты СОУТ на своем сайте', position: 5 },
	{ task: 'Подайте декларацию в ГИТ', position: 6 },
	{ task: 'Ознакомьте работников с результатами', position: 7 },
	{ task: 'Сообщите в СФР о результатах', position: 8 },
])

const isUserFail = ref(null)
const checkStatus = ref(false)

onMounted(() => {
	taskArr.value = taskArr.value.sort(() => Math.random() - 0.5)
})

const checkOrder = () => {
	checkStatus.value = true // Включаем проверку
	const isCorrect = taskArr.value.every(
		(item, index) => item.position === index + 1
	)
	isUserFail.value = !isCorrect
}

const getBorderColor = (element, index) => {
	if (!checkStatus.value) return '0.5vw solid #ffea7e' // По умолчанию
	return element.position === index + 1
		? '0.5vw solid #90ee90'
		: '0.5vw solid red'
}

const nextBlock = () => {
	emit('end', isUserFail.value)
}
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок: 3 СОУТ</h2>
			</div>
			<div class="header_task">
				<h3>
					Определите алгоритм действий, который нужно соблюдать, чтобы не
					нарушить требования законодательства при проведении СОУТ. Расположите
					шаги в нужном порядке.
				</h3>
			</div>
		</div>

		<div class="container__game">
			<draggable
				v-model="taskArr"
				class="draggable-list"
				:animation="200"
				:ghost-class="'drag-ghost'"
				item-key="position"
				:disabled="isUserFail !== null"
			>
				<template #item="{ element, index }">
					<div
						class="container__game_item"
						:style="{ border: getBorderColor(element, index) }"
						:data-index="index + 1"
					>
						{{ element.task }}
					</div>
				</template>
			</draggable>
		</div>

		<div class="container__btn">
			<Transition mode="out-in">
				<Button
					v-if="isUserFail === null"
					key="check"
					color="#fafafa"
					@click="checkOrder"
				>
					Проверить
				</Button>
				<Button
					v-else
					key="next"
					color="#fafafa"
					font-weight="900"
					@click="nextBlock"
				>
					Перейти к блоку Оценка профессиональных рисков
				</Button>
			</Transition>
		</div>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__game {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.container__game_item {
	margin: 2vw 0;
	padding: 1vw 4vw;
	width: 100%;
	text-align: center;
	font-size: 0.95em;
	border: 0.5vw solid #ffea7e;
	background-color: #fef2b8;
	border-radius: 1vw;
	cursor: grab;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.container__game_item::before {
	content: attr(data-index);
	position: absolute;
	top: 20%;
	left: 0;
	transform: translate(-50%, -50%);
	width: 4vw;
	height: 4vw;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1em;
	background-color: #ffea7e;
	border-radius: 50%;
	color: #000;
	font-weight: bold;
}

.container__btn {
	margin-top: 3vw;
	display: flex;
	align-items: center;
	justify-content: center;
}

.result-message {
	margin-top: 2vw;
	text-align: center;
	font-size: 1.2em;
	color: #333;
}

.drag-ghost {
	background-color: #fddb34 !important;
	opacity: 0.8 !important;
	border-color: #fddb34 !important;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
