<script setup>
import Button from '../UI/Button.vue'
import sticker from '@/assets/block_2/sticker.png'

import { ref } from 'vue'
const emit = defineEmits(['end'])

const taskArr = ref([
	{
		task: 'Вам позвонил начальник цеха и сообщил о том, что его работнику попала стружка в глаз, когда он вытачивал деталь на станке. Пострадавшего сразу увезли в больницу. В цехе должны выполнить большой заказ, поэтому на рабочее место травмированного решили поставить другого работника. Не возражаете?',
		btnsText: ['ПОЧЕМУ НЕТ, <br/> ПУСТЬ РАБОТАЕТ', 'ВОЗРАЖАЮ И ЗАПРЕЩАЮ'],
		correct: 'ВОЗРАЖАЮ И ЗАПРЕЩАЮ',
		goodMsg:
			'Когда обстановка, которая была на момент происшествия с пострадавшим не угрожает авариями и травмами другим работникам, ее следует зафиксировать. Не допускайте на рабочее место пострадавшего посторонних людей. Лучше оградите место сигнальной лентой.',
		badMsg:
			'На рабочем месте, где произошел несчастный случай, работу следует остановить, иначе усложните расследование. Позаботьтесь о том, чтобы сохранить обстановку на момент происшествия, если это не грозит травмами и авариями. Не допускайте на рабочее место посторонних, оградите его сигнальной лентой до тех пор, пока не проведете полный осмотр.',
		isFail: null,
	},
	{
		task: 'Пострадавшего работника в больнице госпитализировали. Значит, предстоит расследовать несчастный случай, но пока неясно — легкий он или тяжелый. Нужно выяснить степень тяжести травмы. Направите запрос медикам или будете ждать, пока работник сам с вами свяжется?',
		btnsText: ['НАПИШУ ЗАПРОС В БОЛЬНИЦУ', 'ПОДОЖДУ ПОКА РАБОТНИК ПОЗВОНИТ'],
		correct: 'НАПИШУ ЗАПРОС В БОЛЬНИЦУ',
		goodMsg:
			'Чем раньше получите заключение по форме № 315/у, тем скорее установите категории несчастного случая. Не потеряйте время, которого будет не так много. Если травма легкая, расследовать ее должен работодатель в течение трех календарных дней. Важно также в срок известить о происшествии в госорганы.',
		badMsg:
			'Если у пострадавшего легкая травма, рискуете не успеть. Расследовать такой несчастный случай работодатель обязан в течение трех календарных дней. Важно также известить о происшествии госорганы. Кроме того, не каждый работник сможет назвать точный диагноз.',
		isFail: null,
	},
	{
		task: 'В больнице выдали медицинское заключение о характере и степени тяжести травмы. В нем указали, что травма у работника легкая. Пока не истекли сутки после происшествия, нужно известить о нем госорганы. Сообщать о травме будете только в СФР или дополнительно направите извещение в ГИТ?',
		btnsText: ['СООБЩУ ТОЛЬКО В СФР', 'ИЗВЕЩУ ЕЩЕ И ГИТ'],
		correct: 'СООБЩУ ТОЛЬКО В СФР',
		goodMsg:
			'О легком несчастном случае с застрахованным сообщают только в СФР. Это нужно, чтобы пострадавший вовремя получил страховые выплаты. Расследует легкий несчастный случай работодатель самостоятельно.',
		badMsg:
			'ГИТ и прокуратуру о легком несчастном случае извещать не нужно. Достаточно направить сообщение о страховом случае в СФР. Расследует легкий несчастный случай работодатель самостоятельно.',
		isFail: null,
	},
	{
		task: 'Теперь нужно издать приказ, чтобы собрать комиссию по расследованию. В комиссии должно быть минимум три человека. Кроме председателя в комиссии должен состоять специалист по охране труда. В комиссию предложили включить начальника цеха, в котором произошел несчастный случай. Хорошая идея?',
		btnsText: [
			'НЕТ, НУЖНО РАССЛЕДОВАТЬ ОБЪЕКТИВНО',
			'ДА, ВЕДЬ ТРАВМА ПРОИЗОШЛА У НЕГО НА УЧАСТКЕ',
		],
		correct: 'НЕТ, НУЖНО РАССЛЕДОВАТЬ ОБЪЕКТИВНО',
		goodMsg:
			'В комиссию нельзя включать ответственных за соблюдение требований охраны труда на местах. Также нельзя включать в состав комиссии самого пострадавшего и его представителей.',
		badMsg:
			'Именно поэтому начальнику цеха не место в комиссии. Чтобы объективно расследовать несчастный случай, не включайте в комиссию того, кто отвечает на участке за соблюдение требований охраны труда. Также нельзя состоять в комиссии самому пострадавшему и его представителям.',
		isFail: null,
	},
])

const index = ref(0)
const isShowHit = ref(false)
const isNextButtonDisabled = ref(false)

const checkUserAnswer = e => {
	const userIsCorrectAnswer =
		e.target.innerText === taskArr.value[index.value].correct
	taskArr.value[index.value].isFail = !userIsCorrectAnswer

	isShowHit.value = true
}

const next = () => {
	if (isNextButtonDisabled.value) return // Если кнопка заблокирована, выходим
	isNextButtonDisabled.value = true // Блокируем кнопку

	if (index.value < taskArr.value.length - 1) {
		index.value += 1
	}
	isShowHit.value = false

	setTimeout(() => {
		isNextButtonDisabled.value = false
	}, 500)
}

const nextBlock = () => {
	emit(
		'end',
		taskArr.value.some(item => item.isFail === true)
	)
}
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок 2: Несчастные случаи</h2>
			</div>
			<div class="header_task">
				<h3>
					Вам поручили расследовать несчастный случай. Выберите верные действия
					при расследовании.
				</h3>
			</div>
		</div>

		<div class="container__game">
			<div class="container__game-img">
				<img :src="sticker" alt="" />
			</div>
			<Transition mode="out-in">
				<div class="container__game-text" :key="index">
					{{ taskArr[index].task }}
				</div>
			</Transition>
		</div>

		<Transition mode="out-in">
			<div class="container__game-bts" v-if="!isShowHit">
				<Button
					color="#fafafa"
					backgroundColor="#696b74"
					hoverBackgroundColor="#757884"
					v-for="item in taskArr[index].btnsText"
					v-html="item"
					@click="checkUserAnswer"
				>
				</Button>
			</div>
			<div v-else class="container__game_hit">
				<div class="container__game_hit-head">
					<div v-if="!taskArr[index].isFail">
						<h4 class="good">ВЕРНО!</h4>
						<p>{{ taskArr[index].goodMsg }}</p>
					</div>
					<div v-else>
						<h4 class="bad">НЕВЕРНО!</h4>
						<p>{{ taskArr[index].badMsg }}</p>
					</div>
				</div>
				<div class="container__game_hit-btn">
					<Button
						v-if="taskArr.length > index + 1"
						color="#fafafa"
						@click="next"
						:disabled="isNextButtonDisabled"
						>ДАЛЬШЕ</Button
					>
					<Button v-else color="#fafafa" font-weight="900" @click="nextBlock"
						>Перейти к блоку СОУТ</Button
					>
				</div>
			</div></Transition
		>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__game {
	margin: 0 2vw;
	display: grid;
	grid-template-columns: 0.3fr 1fr;
	align-items: center;
}

.container__game-img img {
	width: 20vw;
}

.container__game-text {
	font-weight: 900;
	text-align: justify;
	color: #494949;
	font-size: 1.1em;
}

.container__game-bts {
	margin-top: 4vw;
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 18vw;
	margin: 4vw 4vw;
}

.container__game_hit {
	margin: 5vw 14vw;
}

.container__game_hit-head p {
	text-align: justify;
	font-size: 1em;
}

.good {
	color: #8cd147;
	text-align: center;
	font-size: 1.4em;
	margin-bottom: 1vw;
}

.bad {
	color: red;
	text-align: center;
	font-size: 1.4em;
	margin-bottom: 1vw;
}

.container__game_hit-btn {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}
</style>
