<script setup>
import image from '@/assets/block_4/image.png'

import Button from '../UI/Button.vue'
import { ref } from 'vue'

const emit = defineEmits(['end'])
const index = ref(0)
const isShowNextBlock = ref(false)

const taskArr = ref([
	{
		task: 'Опасность поражения током',
		hit: 'На этом рабочем месте получить удар током можно повсюду. Вы сами убедились в этом, когда смотрели подсказки',
		correct: 'Есть',
		isFail: null,
	},
	{
		task: 'Опасность из-за укуса переносчиков инфекций',
		hit: 'Как правило, в офисах нет такой опасности, если вы не живете в какой-нибудь экзотической стране :)',
		correct: 'Нет',
		isFail: null,
	},
	{
		task: 'Опасность падения и-за потери равновесия, в том числе при спотыкании или подскальзывании',
		hit: 'Такая опасность есть. Сотрудник может запнуться о провода, ковер, а также подскользнуться на мокром полу',
		correct: 'Есть',
		isFail: null,
	},
	{
		task: 'Опасность от недостатка кислорода в замкнутых технологических емкостях',
		hit: 'Сотрудник не находится в технологической емкости, поэтому такой опасности нет',
		correct: 'Нет',
		isFail: null,
	},
	{
		task: 'Опасность, связанная с допуском работников, не прошедших подготовку по охране труда',
		hit: 'Каждый сотрудник на рабочем месте должен заниматься своими обязанностями. Электрик должен чинить электроприборы, которые поломались. У обычного офисного сотрудника нет специальной подготовки и соответствующей группы по электробезопасности, чтобы выполнять такие функции. Он может получить травму на рабочем мест',
		correct: 'Есть',
		isFail: null,
	},
])

const checkAnswer = e => {
	const isCorrect = e === taskArr.value[index.value].correct

	taskArr.value[index.value].isFail = !isCorrect

	if (index.value === taskArr.value.length - 1) {
		isShowNextBlock.value = true
	}
}

const isNextButtonDisabled = ref(false)

const next = () => {
	if (isNextButtonDisabled.value) return // Если кнопка заблокирована, выходим
	isNextButtonDisabled.value = true // Блокируем кнопку

	if (index.value < taskArr.value.length - 1) {
		index.value += 1
	}

	// Разблокируем кнопку через 500 мс
	setTimeout(() => {
		isNextButtonDisabled.value = false
	}, 500)
}

const nextBlock = () => {
	const hasFailures = taskArr.value.some(item => item.isFail === true)
	emit('end', hasFailures)
}
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок 4: Оценка профессиональных рисков</h2>
			</div>
			<div class="header_task">
				<h3>
					Изучите картинку и подумайте, какие опасности есть на этом рабочем
					месте. Далее решите пять упражнений и определите, есть ли такая
					опасность на картинке в офисе или нет.
				</h3>
			</div>
		</div>

		<div class="container__img">
			<img :src="image" alt="" />
		</div>

		<div class="container__game">
			<Transition mode="out-in">
				<div :key="index" class="container__game_item">
					<Transition mode="out-in">
						<div
							v-if="taskArr[index].isFail === null"
							class="container__game_item-task"
						>
							{{ taskArr[index].task }}
						</div>
						<div v-else class="container__game_item-hit">
							<div v-if="!taskArr[index].isFail">
								<h3>Вы правы</h3>
								<p>{{ taskArr[index].hit }}</p>
							</div>
							<div v-else>
								<h3>Вы ошиблись</h3>
								<p>{{ taskArr[index].hit }}</p>
							</div>
						</div>
					</Transition>
					<Transition mode="out-in">
						<div
							v-if="taskArr[index].isFail === null"
							class="container__game_item-btns"
						>
							<button class="button-game" @click="checkAnswer('Есть')">
								Есть
							</button>
							<button class="button-game" @click="checkAnswer('Нет')">
								Нет
							</button>
						</div>
						<div v-else class="container__game_item-btn">
							<button
								v-if="taskArr.length > index + 1"
								class="button-game"
								@click="next"
								:disabled="isNextButtonDisabled"
							>
								Далее
							</button>
						</div>
					</Transition>
				</div>
			</Transition>
		</div>

		<Transition>
			<div v-if="isShowNextBlock" class="container__btn-next">
				<Button @click="nextBlock" color="#fafafa" font-weight="900"
					>Перейти к блоку Медицинские осмотры</Button
				>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__img img {
	width: 100vw;
}

.container__game {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}

.container__game_item {
	background-color: #f5f6fa;
	border-radius: 2vw;
	min-height: 20vw;
	width: 70vw;
	padding: 2vw;
}

.container__game_item-task {
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: center;
	height: 70%;
	text-align: center;
}

.container__game_item-btns {
	margin: 1vw 0;
	display: flex;
	justify-content: space-around;
}

.button-game {
	padding: 1vw 2vw;
	cursor: pointer;
	transition: background-color 0.3s;
	font-size: 1em;
	border-collapse: collapse;
	box-sizing: border-box;
	border-radius: 1vw;
}

.container__game_item-btn {
	margin-top: 1vw;
	display: flex;
	justify-content: center;
}

.container__game_item-hit h3 {
	margin-bottom: 1vw;
	text-align: center;
}

.container__game_item-hit p {
	text-align: justify;
}

.container__btn-next {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
	animation: show 1s ease;
}

@keyframes show {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
</style>
