<script setup>
import Button from '../UI/Button.vue'
import image from '@/assets/block_5/image.png'
import { ref } from 'vue'

const inputValue = ref(0)
const correct = 3
const isUserFail = ref(null)
const emit = defineEmits(['end'])

const handleInput = event => {
	const value = event.target.value
	const parsedValue = parseInt(value, 10)

	if (isNaN(parsedValue) || parsedValue < 0) {
		inputValue.value = 0
	} else if (parsedValue > 10) {
		inputValue.value = 10
	} else {
		inputValue.value = parsedValue
	}
}

const checkUserAnswer = () => {
	isUserFail.value = inputValue.value !== correct
}

const nextBlock = () => {
	emit('end', isUserFail.value)
}
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок 5: Медицинские осмотры</h2>
			</div>
			<div class="header_task">
				<h3>
					Перед вами поименный список работников, которые подлежат
					периодическому медосмотру. Именно в таком виде документ направят в
					медорганизацию на согласование. Но этот документ составили с ошибками.
					Изучите документ, найдите ошибки и в отельное окно введите цифру с
					количеством ошибок.
				</h3>
			</div>
		</div>

		<div class="container__img">
			<img :src="image" alt="Документ" />
		</div>

		<Transition mode="out-in">
			<div v-if="isUserFail === null" class="container__game">
				<label for="errors">Введите количество ошибок:</label>
				<input
					type="number"
					id="errors"
					name="errors"
					v-model="inputValue"
					@input="handleInput"
					class="styled-input"
					placeholder="0"
				/>
				<Button color="#fafafa" @click="checkUserAnswer">Проверить</Button>
			</div>
			<div v-else class="container__next">
				<div class="error" v-if="isUserFail">Вы ошиблись!</div>
				<div class="good" v-else>Вы ответили верно!</div>
				<Button @click="nextBlock" color="#fafafa" font-weight="900"
					>Перейти к блоку Обучение по ОТ
				</Button>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__img {
	display: flex;
	justify-content: center;
}

.container__game {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

label {
	font-size: 1em;
}

.styled-input {
	margin: 0 2vw;
	width: 10vw;
	padding: 10px;
	font-size: 1em;
	border: 2px solid #ccc;
	border-radius: 5px;
	outline: none;
	transition: border-color 0.3s;
	appearance: textfield;
}

.styled-input:focus {
	border-color: #007bff;
}

.styled-input::placeholder {
	color: #aaa;
	font-style: italic;
}

/* Remove spinner buttons for number inputs in Webkit browsers */
.styled-input::-webkit-outer-spin-button,
.styled-input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.container__next {
	margin-top: 1vw;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: column;
	gap: 1vw;
}

.good {
	color: #42d072;
	font-size: 1.2em;
	font-weight: 900;
}

.error {
	color: red;
	font-size: 1.2em;
	font-weight: 900;
}
</style>
