<script setup>
import Button from '../UI/Button.vue'
import { ref, onMounted } from 'vue'

const task = ref({
	text: 'На предприятии проводят обучение и проверяют знания по программам А, Б, оказание первой помощи и использование СИЗ. Для проверки знаний работодатель создал единую комиссию по проверке знания требований охраны труда. <br/> Её члены прошли обучение в учебном центре только по тем программам, по которым проверяют знания в организации. <br/> <br/> Правильно ли работодатель создал комиссию?',
	correct: 'НЕПРАВИЛЬНО',
	isFail: null,
})

const userAnswer = e => {
	const isCorrect = e === task.value.correct
	task.value.isFail = !isCorrect
}

const emit = defineEmits(['end'])

const nextBlock = () => {
	emit('end', task.value.isFail)
}
</script>

<template>
	<div class="container">
		<div class="header">
			<div class="header_block">
				<h2>Блок 6: Обучение по ОТ</h2>
			</div>
			<div class="header_task">
				<h3>
					Изучите ситуационную задачу и определите, верно ли работодатель создал
					комиссию по проверке знания требований охраны труда.
				</h3>
			</div>
		</div>

		<Transition mode="out-in">
			<div v-if="task.isFail === null">
				<div class="container__game">
					<div class="container__game_text" v-html="task.text"></div>
				</div>
				<div class="container__btns">
					<Button
						@click="userAnswer('ПРАВИЛЬНО')"
						color="#fafafa"
						backgroundColor="#696b74"
						hoverBackgroundColor="#7f828b"
						font-weight="900"
						>ПРАВИЛЬНО</Button
					>
					<Button
						@click="userAnswer('НЕПРАВИЛЬНО')"
						color="#fafafa"
						backgroundColor="#696b74"
						hoverBackgroundColor="#7f828b"
						font-weight="900"
						>НЕПРАВИЛЬНО</Button
					>
				</div>
			</div>
			<div v-else class="container__hit">
				<div v-if="task.isFail">
					<h2 class="error">Не совсем так :(</h2>
				</div>
				<div v-else><h2 class="good">Отлично!</h2></div>
				<div class="container__hit_text">
					<div>Работодатель создал комиссию неправильно</div>
					<div>
						Работодатель создал единую комиссию. Членов единой комиссии обучили
						по программам А, Б, СИЗ и первой помощи, но не обучили программе В.
						<br />
						При контрольных мероприятиях инспектор ГИТ указал на нарушение
						пункта 77 Правил № 2464, так как членов единой комиссии не обучили
						по программе В.
					</div>
				</div>
				<div class="container__btn">
					<Button @click="nextBlock" color="#fafafa" font-weight="900"
						>Посмотреть результат <br />
						и получить индивидуальную подборку программ</Button
					>
				</div>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.header {
	font-size: 0.9em;
}

.header_task {
	margin: 2vw 0;
	display: flex;
	justify-content: center;
}

.header_task h3 {
	width: 80vw;
	text-align: justify;
}

.container__game {
	margin-top: 2vw;
	display: flex;
	align-items: center;
	flex-flow: column;
}

.container__game_text {
	width: 70vw;
	text-align: justify;
	color: #fafafa;
	background-color: #657df9;
	font-weight: 900;
	padding: 2vw;
	border-radius: 2vw;
}

.container__btns {
	margin-top: 2vw;
	display: flex;
	justify-content: space-evenly;
}

.container__hit {
	display: flex;
	flex-flow: column;
	align-items: center;
}

.good {
	color: #42d072;
	font-size: 1.2em;
	font-weight: 900;
}

.error {
	color: red;
	font-size: 1.2em;
	font-weight: 900;
}

.container__hit_text {
	margin: 1vw 0;
	width: 65vw;
	text-align: justify;
	font-weight: 900;
}
</style>
