<script setup>
import StartPage from './components/StartPage.vue'
import BlockOne from './components/Blocks/BlockOne.vue'
import BlockTwo from './components/Blocks/BlockTwo.vue'
import BlockThree from './components/Blocks/BlockThree.vue'
import BlockFour from './components/Blocks/BlockFour.vue'
import BlockFive from './components/Blocks/BlockFive.vue'
import BlockSix from './components/Blocks/BlockSix.vue'
import ResultPage from './components/ResultPage.vue'

import { ref, computed } from 'vue'

const isStartGame = ref(false)
const resultArr = ref([
	{
		text: 'Обеспечение СИЗ: как организовать работу в компании с нуля',
		isBad: null,
		link: 'https://links.action-media.ru/D0Cb',
		themeText: 'СИЗ',
	},
	{
		text: 'Несчастный случай в компании: как действовать специалисту по охране труда',
		isBad: null,
		link: 'https://links.action-media.ru/DRZW',
		themeText: ' Несчастные случаи',
	},
	{
		text: 'СОУТ: как организовать и проконтролировать процедуру в компании',
		isBad: null,
		link: 'https://links.action-media.ru/mNLx',
		themeText: 'СОУТ',
	},
	{
		text: 'Оценка и управление профессиональными рисками',
		isBad: null,
		link: 'https://links.action-media.ru/AxDL ',
		themeText: 'Оценка профессиональных рисков',
	},
	{
		text: 'Медосмотры и психосвидетельствования: организуем процесс без ошибок',
		isBad: null,
		link: 'https://links.action-media.ru/DRZa',
		themeText: 'Медицинские осмотры',
	},
	{
		text: 'Обучение по охране труда: организация, проведение, оформление документов',
		isBad: null,
		link: 'https://links.action-media.ru/mNL0',
		themeText: 'Обучение по ОТ',
	},
])
const currentBlock = ref(1)
const isShowResult = ref(false)

const failArr = computed(() => {
	return resultArr.value.filter(item => item.isBad)
})

const nextBlock = e => {
	resultArr.value[currentBlock.value - 1].isBad = e
	currentBlock.value += 1
}

const showResult = e => {
	resultArr.value[currentBlock.value - 1].isBad = e
	isShowResult.value = true
}
</script>

<template>
	<Transition mode="out-in">
		<div v-if="!isShowResult">
			<Transition mode="out-in">
				<div v-if="!isStartGame"><StartPage @start="isStartGame = true" /></div>
				<div class="container" v-else>
					<Transition mode="out-in">
						<div v-if="currentBlock === 1">
							<BlockOne @end="nextBlock" />
						</div>
						<div v-else-if="currentBlock === 2">
							<BlockTwo @end="nextBlock" />
						</div>
						<div v-else-if="currentBlock === 3">
							<BlockThree @end="nextBlock" />
						</div>
						<div v-else-if="currentBlock === 4">
							<BlockFour @end="nextBlock" />
						</div>
						<div v-else-if="currentBlock === 5">
							<BlockFive @end="nextBlock" />
						</div>
						<div v-else-if="currentBlock === 6">
							<BlockSix @end="showResult" />
						</div>
					</Transition>
				</div>
			</Transition>
		</div>

		<div v-else>
			<ResultPage :failArr="failArr" />
		</div>
	</Transition>

	<footer></footer>
</template>

<style scoped>
footer {
	height: 2vw;
}
</style>
